* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

ul,ol {
    list-style: none;
}

.App {
    font-family: "Noto Sans JP", sans-serif;
    font-size: 18px;
}

header {
    display: flex;
    justify-content: space-between;
    background: #333;
    color: #fff;
    padding: 20px;
    nav {
        ul {
            display: flex;
            li {
                margin-inline: 10px;
                a {
                    color: #fff;
                }
            }
        }
    }
}